<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12  md8 offset-md2> <h4 class="text-xs-center">
        My registered meetups
      </h4>
      </v-flex> 
    </v-layout>
    <v-layout row wrap v-for="meetup in meetups" :key='meetup.id' class='mb-2'>
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
          <v-card class="info" >
              <v-container fluid>
                <v-layout row>
                  <v-flex xs5 md3>
                    <v-card-media
                      :src="meetup.imageUrl"
                      height="125px"
                    ></v-card-media>
                  </v-flex>
                      <v-flex xs7 md9>
                        <v-card-title>
                          <div>
                            <h5 class="white--text">{{meetup.title}}</h5>
                            <div>{{meetup.date | date}}</div>
                            </div>
                            </v-card-title>
                          <v-card-actions>
                            <v-btn flat  :to="'/meetup/'+ meetup.id">
                              <v-icon left>arrow_forward</v-icon>
                              View meetup
                            </v-btn>
                          </v-card-actions>
                  </v-flex>
                </v-layout>
              </v-container>
          </v-card>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12  md8 offset-md2> <h4 class="text-xs-center">
        My created meetups
      </h4>
      </v-flex> 
    </v-layout>
    <v-layout row wrap v-for="meetup in myCreatedMeetups" :key='meetup.id' class='mb-2'>
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
          <v-card class="info" >
              <v-container fluid>
                <v-layout row>
                  <v-flex xs5 md3>
                    <v-card-media
                      :src="meetup.imageUrl"
                      height="125px"
                    ></v-card-media>
                  </v-flex>
                      <v-flex xs7 md9>
                        <v-card-title>
                          <div>
                            <h5 class="white--text">{{meetup.title}}</h5>
                            <div>{{meetup.date | date}}</div>
                            </div>
                            </v-card-title>
                          <v-card-actions>
                            <v-btn flat  :to="'/meetup/'+ meetup.id">
                              <v-icon left>arrow_forward</v-icon>
                              View meetup
                            </v-btn>
                          </v-card-actions>
                  </v-flex>
                </v-layout>
              </v-container>
          </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      myMeetups: []
    }
  },
  computed: {
    meetups () {
      let allMeetups =  this.$store.getters.loadedMeetups
      let registeredMeetups = this.$store.getters.user.registeredMeetups
      let regMeetups = []
allMeetups.filter(function(item){
  for (let j =0; j<=registeredMeetups.length; j++) {
if (item.id == registeredMeetups[j]) regMeetups.push(item) 
  }
  return regMeetups
})
 return regMeetups

    },
    myCreatedMeetups () {
      let allMeetups =  this.$store.getters.loadedMeetups
      let myId = this.$store.getters.user.id
      allMeetups.forEach((item, i, allMeetups)=>{
        if (myId === item.creatorId) {
           this.myMeetups.push(item)
        } 
        })
      return this.myMeetups
    }
  }
}
</script>



