<template>
  <v-container>
      <v-layout row wrap else>
          <v-flex xs12>
              <v-card>
                  <v-card-title>
                      <h6 class="primary--text">{{meetup.title}}</h6> 
                      <template v-if='userIsCreator'>
                          <v-spacer></v-spacer>
                          <app-edit :meetup = 'meetup'></app-edit>
                      </template>
                  </v-card-title>
                         <v-card-media
                      :src="meetup.imageUrl"
                      height="400px"
                    ></v-card-media>
                    <v-card-text>
                        <div class="info--text">{{meetup.date | date}} - {{meetup.location}}</div>
                        <div><app-edit-time :meetup = 'meetup' v-if='userIsCreator'></app-edit-time>
                        <app-edit-hours :meetup = 'meetup' v-if='userIsCreator'></app-edit-hours></div>
                        <div>{{meetup.description}}</div>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <app-RegistrationDialog :meetupId="meetup.id"
                        v-if='userIsAuth && !userIsCreator'></app-RegistrationDialog>
                    </v-card-actions>
              </v-card>
          </v-flex>
      </v-layout>
  </v-container>
</template>
<script>
export default {
    props: ['id'],
  computed: {
      meetup () {
          return this.$store.getters.loadedMeetup(this.id)
      },
      userIsAuth() {
          return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
      userIsCreator () {
          if (!this.userIsAuth) {
              return false 
          } else {
              return this.$store.getters.user.id == this.meetup.creatorId
          }
      },
      loading (){
      return this.$store.getters.loading
    }
  }
}
</script>




