
import * as firebase from 'firebase'


export default {
    state: {
        loadedMeetups: [
            {imageUrl: 'https://media.timeout.com/images/103678916/image.jpg', 
            id: 'fgnfgn', title: 'Meet up in NY', date: new Date(), location: 'New York', description: 'Meetup in NY near liberty statue'}, 
            {imageUrl: 'https://typeset-beta.imgix.net/rehost%2F2016%2F9%2F13%2F036c7f63-e5a7-4358-be85-cde1a36954ad.jpg', 
            id: 'dghnnnhgn', title: 'Meet up in Paris', date: new Date(), location: 'Paris', description: 'see Paris and die'}
        ],

        loading: false,
        error: null
    },
    mutations: {
        

        setMeetups (state, payload){
            state.loadedMeetups = payload
        },
        
        createMeetup (state, payload) {
            state.loadedMeetups.push(payload)
        },
        updateMeetup (state, payload) {
            const meetup = state.loadedMeetups.find((meetup)=>{
                return meetup.id === payload.id
            })
            if (payload.title) {
                meetup.title = payload.title
            }
            if (payload.description) {
                meetup.description = payload.description
            }
            if (payload.date) {
                meetup.date = payload.date
            }
        },

        setLoading (state, payload) {
            state.loading = payload
        },
        setError (state, payload) {
            state.error = payload
        },
        clearError (state) {
            state.error = null
        }
    },
    actions: {
        
        loadMeetups ({commit,getters}){
            commit('setLoading', true)
            firebase.database().ref('meetups').once('value').then(
                (data)=>{
                    const meetups = []
                    const obj = data.val()
                    for (let key in obj) {
                        meetups.push({
                            id: key,
                            title: obj[key].title,
                            description: obj[key].description,
                            imageUrl: obj[key].imageUrl,
                            date: obj[key].date,
                            location: obj[key].location,
                            creatorId: obj[key].creatorId
                        })
                    }
                    commit('setMeetups', meetups)
                    commit('setLoading', false)
                }
            ).catch(
                (error)=>{console.log(error) 
                    commit('setLoading', false)}
            )
        },
        createMeetup ({commit, getters}, payload) {
      const meetup = {
        title: payload.title,
        location: payload.location,
        description: payload.description,
        date: payload.date.toISOString(),
        creatorId: getters.user.id
      }
      let imageUrl
      let key
      firebase.database().ref('meetups').push(meetup)
        .then((data) => {
          key = data.key
          return key
        })
        .then(key => {
          const filename = payload.image.name
          const ext = filename.slice(filename.lastIndexOf('.'))
          return firebase.storage().ref('meetups/' + key + '.' + ext).put(payload.image)
        })
        .then(fileData => {
          imageUrl = fileData.metadata.downloadURLs[0]
          return firebase.database().ref('meetups').child(key).update({imageUrl: imageUrl})
        })
        .then(() => {
          commit('createMeetup', {
            ...meetup,
            imageUrl: imageUrl,
            id: key
          })
        })
        .catch((error) => {
          console.log(error)
        })
      // Reach out to firebase and store it
    },
    updateMeetupData ({commit}, payload) {
        commit('setLoading', true)
        const updateObj = {}
        if (payload.title) {
            updateObj.title = payload.title
        }
        if (payload.description) {
            updateObj.description = payload.description
        }
        if (payload.date) {
            updateObj.date = payload.date
        }
        firebase.database().ref('meetups').child(payload.id).update(updateObj).then(
            ()=>{
                commit('setLoading', false) 
                commit('updateMeetup', payload)
            }
        ).catch((error)=>{
            commit('setLoading', false) 
            console.log(error)})

    },
    clearError ({commit}){
        commit('clearError')
    }

},

    getters: {
        loadedMeetups (state) {
            return state.loadedMeetups.sort((meetupA, meetupB) => {
                return meetupA.date > meetupB.date
            })
        },
        loadedMeetup (state) {
            return (meetupId) => {
        return state.loadedMeetups.find((meetup) => {
                  return meetup.id === meetupId
                })
            }
        },
        featuredMeetups (state, getters) {
            return getters.loadedMeetups.slice(0, 5)
        },
         
            loading (state) {
                return state.loading
            },
            error (state) {
                return state.error
            }
        }
    }



