
import Vue from 'vue'

import Vuetify from 'vuetify'
import './stylus/main.styl'

import App from './App'
import router from './router'
import * as firebase from 'firebase'

import {store} from './store'
import DateFilter from './filters/date'

import Alert from './components/shared/Alert.vue'
import editMeetup from './components/Meetup/Edit/editMeetup.vue'
import EditTime from  './components/Meetup/Edit/EditTime.vue'
import EditHours from  './components/Meetup/Edit/EditHours.vue'
import RegistrationDialog from './components/Meetup/Registration/RegistrationDialog.vue'

Vue.use(Vuetify)
Vue.component('app-alert', Alert)
Vue.component('app-edit', editMeetup)
Vue.component('app-edit-time', EditTime)
Vue.component('app-edit-hours', EditHours)
Vue.component('app-RegistrationDialog', RegistrationDialog)

Vue.config.productionTip = false
Vue.filter('date', DateFilter)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
  created () {
    firebase.initializeApp ({
      apiKey: "AIzaSyAdofel6vyY_IJireTXal8WVf3dscrQeKk",
      authDomain: "devmeetup-88316.firebaseapp.com",
      databaseURL: "https://devmeetup-88316.firebaseio.com",
      projectId: "devmeetup-88316",
      storageBucket: "gs://devmeetup-88316.appspot.com"
    })
    

    this.$store.dispatch('loadMeetups')
    
    firebase.auth().onAuthStateChanged((user)=>{
      if(user){
        this.$store.dispatch('autoSignIn', user)
        this.$store.dispatch('fetchUserData')
      } 
    })
    
  }
})



