<template>
<v-dialog max-width="290" persistent v-model="editDialog">
    <v-btn accent slot="activator">Edit Date </v-btn>
    <v-container class="white">
        <v-layout row wrap>
            <v-flex xs12>
                <v-card-title>Edit meetup date</v-card-title>
            </v-flex>
        </v-layout>
                    <v-divider></v-divider>
        <v-layout row wrap >
            <v-flex xs12>
               <v-date-picker v-model="editDate" style="width: 100%" actions>
                   <template slot-scope="{save, cancel}">
                <v-btn
                  class="blue--text darken-1"
                  flat
                  @click.native="editDialog = false">Close</v-btn>
                <v-btn
                  class="blue--text darken-1"
                  flat
                  @click.native="onSaveChanges">Save</v-btn>
              </template>
                   </v-date-picker>    
            </v-flex>
        </v-layout>
    </v-container>
</v-dialog>
</template>

<script>
export default {
    props: ['meetup'],
  data () {
      return {
          editDialog: false,
          editDate: null
      }
  },
  methods: {
      onSaveChanges () {
          const newDate = new Date(this.meetup.date)
          const newDay = new Date(this.editDate).getUTCDate()
          const newMonth = new Date(this.editDate).getUTCMonth()
          const newYear = new Date(this.editDate).getUTCFullYear()
          newDate.setUTCDate(newDay)
          newDate.setUTCMonth(newMonth)
          newDate.setUTCFullYear(newYear)
          this.$store.dispatch('updateMeetupData', {
              id: this.meetup.id,
              date: newDate
          }) 
          this.editDialog = false
      }
  },
  created () {
      this.editDate = new Date ()
  }
}
</script>




