<template>
<v-container>
  <v-layout row>
    <v-flex xs12 sm6 offset-sm3>
      <h4>Create a new Meetup</h4>
    </v-flex>
  </v-layout>
    <v-layout row>
    <v-flex xs12>
      <form @submit.prevent="onCreateMeetup">
        <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
            <v-text-field name='title' label="Title" id='title' required v-model="title">
            </v-text-field>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
            <v-text-field name='location' label="Location" id='location' required v-model="location">
            </v-text-field>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
         <v-btn raised class="primary" @click="onPickFile">Upload image</v-btn>
         <input type="file" 
         style="display: none" 
         ref='fileInput' 
         accept="image/*"
         @change="onFilePicked">
         </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
        <img :src="imageUrl" height="150">
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
            <v-text-field name='description' label="Description" id='description' multi-line required v-model="description">
            </v-text-field>
          </v-flex>
        </v-layout>
              <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
           <h4>Choose a date & time</h4>
          </v-flex>
        </v-layout>
                   <v-layout row class="mb-2">
          <v-flex xs12 sm6 offset-sm3>
          <v-date-picker v-model="date" landscape></v-date-picker>
          </v-flex>
        </v-layout>
                         <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
          <v-time-picker v-model="time" landscape format="24hr"></v-time-picker>
          </v-flex>
        </v-layout>
            <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
            <v-btn class="primary" :disabled="!formIsValid" type='submit'>CREATA MEETUP</v-btn>
          </v-flex>
        </v-layout>
        
      </form>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
export default {
  data () {
    return {
      title: '',
      location: '',
      imageUrl: '',
      description: '',
      date: new Date(),
      time: new Date(),
      image: null
    }
  },
  computed: {
    formIsValid () {
      return this.title !== '' && this.location !== '' && this.imageUrl !== '' && this.description !== ''
    },
    subDateTime () {
      const date = new Date(this.date)
      if (typeof this.time === 'string') {
        let hours = this.time.match(/^(\d+)/)[1]
        const minutes = this.time.match(/:(\d+)/)[1]
        date.setHours(hours)
        date.setMinutes(minutes)
      } else {
        date.setHours(this.time.getHours())
        date.setMinutes(this.time.getMinutes())
      }
      return date
    }
  },
  methods: {
    onCreateMeetup () {
      if (!this.formIsValid){
        return
      }
      const meetupData = {
         title: this.title,
      location: this.location,
      image: this.image,
      description: this.description,
      date: this.subDateTime
      }
      this.$store.dispatch('createMeetup', meetupData)
      this.$router.push('/meetups')
    },
    onPickFile () {
      this.$refs.fileInput.click()
    },
    onFilePicked (event) {
      const files = event.target.files
      let fileName = files[0].name
      if(fileName.lastIndexOf('.')<=0) {
        return alert('Add valid file, please')
      }
      const fileReader = new FileReader() 
      fileReader.addEventListener('load', ()=>{
        this.imageUrl = fileReader.result
      })
      fileReader.readAsDataURL(files[0])
      this.image = files[0]
    }
  }
}
</script>



