<template>
  <v-container>
    <v-layout>
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
         <v-text-field
              name="input-1"
              label="Search meetup's location"
              id="testing"
              v-model="searchLocation"
            ></v-text-field>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-for="meetup in meetups" :key='meetup.id' class='mb-2'>
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
          <v-card class="info" >
              <v-container fluid>
                <v-layout row>
                  <v-flex xs5 md3>
                    <v-card-media
                      :src="meetup.imageUrl"
                      height="125px"
                    ></v-card-media>
                  </v-flex>
                      <v-flex xs7 md9>
                        <v-card-title>
                          <div>
                            <h5 class="white--text">{{meetup.title}}</h5>
                            <div>{{meetup.date | date}}</div>
                            </div>
                            </v-card-title>
                          <v-card-actions>
                            <v-btn flat  :to="'/meetup/'+ meetup.id">
                              <v-icon left>arrow_forward</v-icon>
                              View meetup
                            </v-btn>
                          </v-card-actions>
                  </v-flex>
                </v-layout>
              </v-container>
          </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      searchLocation: ''
    }
  },
  computed: {
    meetups () {
        let allMeetups = this.$store.getters.loadedMeetups
        let search = this.searchLocation.toLowerCase()
        let filteredMeetups = []
        allMeetups.filter(function(el){
          if (el.location.toLowerCase().indexOf(search)>-1) 
            filteredMeetups.push(el)    
        })
        return filteredMeetups
      //return this.$store.getters.loadedMeetups
    }
  }
}
</script>



