<template>
<v-dialog max-width="290" persistent v-model="editDialog">
    <v-btn accent slot="activator">Edit Time </v-btn>
    <v-container class="white">
        <v-layout row wrap>
            <v-flex xs12>
                <v-card-title>Edit meetup time</v-card-title>
            </v-flex>
        </v-layout>
                    <v-divider></v-divider>
        <v-layout row wrap >
            <v-flex xs12>
               <v-time-picker v-model="editTime" style="width: 100%" actions format="24hr">
                   <template slot-scope="{save, cancel}">
                <v-btn
                  class="blue--text darken-1"
                  flat
                  @click.native="editDialog = false">Close</v-btn>
                <v-btn
                  class="blue--text darken-1"
                  flat
                  @click.native="onSaveChanges">Save</v-btn>
              </template>
                   </v-time-picker>    
            </v-flex>
        </v-layout>
    </v-container>
</v-dialog>
</template>

<script>
export default {
    props: ['meetup'],
  data () {
      return {
          editDialog: false,
          editTime: null
      }
  },
  methods: {
  onSaveChanges () {
        const newDate = new Date(this.meetup.date)
        const hours = this.editTime.match(/^(\d+)/)[1]
        const minutes = this.editTime.match(/:(\d+)/)[1]
        newDate.setHours(hours)
        newDate.setMinutes(minutes)
        this.$store.dispatch('updateMeetupData', {
          id: this.meetup.id,
          date: newDate
        }) 
        this.editDialog = false
      }
  },

  }

</script>




